@import url(https://fonts.googleapis.com/css?family=Karla:400,700&display=swap);
body,
html {
  margin: 0;
}

.App {
  max-width: 414px;
  width: 100%;
  margin: 0 auto;
  max-height: 896px;
  height: 100vh;
  background-color: #2c3039;
  position: relative;
}

.App * {
  font-family: "Karla", sans-serif;
}

.App * {
  color: #fff;
}

.hero {
  height: 50%;
  position: relative;
}

.hero::before,
.hero::after {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  content: "";
}

.hero::before {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#3d3f49), to(#9500ed));
  background-image: linear-gradient(180deg, #3d3f49 0%, #9500ed 100%);
  opacity: 0.5;
  z-index: 1;
}

.hero::after {
  background-image: url(/OnTrack/static/media/bg.856358a4.jpg);
  opacity: 0.35;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;
  z-index: 2;
  mix-blend-mode: multiply;
}

.hero .heroContent {
  width: 175px;
  margin: 0 auto;
  position: relative;
  z-index: 3;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: center;
}

.hero .heroContent h1 {
  font-size: 42px;
  margin: 0;
}

.graphicContainer {
  display: grid;
  grid-template-columns: auto 1fr auto;
}

.graphicContainer ul {
  margin: 0;
  list-style: none;
  padding: 0;
  text-align: center;
  padding-top: 3px;
}

.graphicContainer ul li {
  display: inline-block;
  margin: 0 7px;
  vertical-align: middle;
  border-radius: 100%;
  background: white;
  position: relative;
}

.graphicContainer ul:first-child li:last-child,
.graphicContainer ul:last-child li:first-child {
  width: 8px;
  height: 8px;
}

.graphicContainer ul li:nth-child(2) {
  width: 6px;
  height: 6px;
  opacity: 0.6;
}

.graphicContainer ul:first-child li:first-child,
.graphicContainer ul:last-child li:last-child {
  width: 4px;
  height: 4px;
  opacity: 0.4;
}

.graphicContainer ul:first-child li:first-child {
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
}

.graphicContainer ul:first-child li:nth-child(2) {
  -webkit-animation-delay: 0.35s;
          animation-delay: 0.35s;
}

.graphicContainer ul:first-child li:last-child {
  -webkit-animation-delay: 0.45s;
          animation-delay: 0.45s;
}

img {
  width: 42px;
  -webkit-animation-delay: 0.55s;
          animation-delay: 0.55s;
  padding: 0 5px;
  margin: 0 auto;
  position: relative;
  top: 6px;
}

.graphicContainer ul:last-child li:first-child {
  -webkit-animation-delay: 0.65s;
          animation-delay: 0.65s;
}

.graphicContainer ul:last-child li:nth-child(2) {
  -webkit-animation-delay: 0.75s;
          animation-delay: 0.75s;
}

.graphicContainer ul:last-child li:last-child {
  -webkit-animation-delay: 0.85s;
          animation-delay: 0.85s;
}

.controls {
  padding: 30px 25px;
  height: 50%;
}

.dropDownContainer {
  position: relative;
  margin-bottom: 25px;
}

.dropDownContainer .locationBox {
  background: #43444d;
  border-radius: 10px;
  box-shadow: inset 0 1px 5px 0 rgba(0, 0, 0, 0.75);
  position: relative;
  z-index: 3;
}

.dropDownContainer .locationBox p {
  margin: 0;
  font-size: 18px;
  opacity: 0.5;
  padding: 20px 25px;
}

.dropDownContainer .locationBox img {
  position: absolute;
  right: 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 15px;
}

.dropDownContainer.selected .locationBox p {
  opacity: 1;
}

.dropDownContainer .locationList {
  margin: 0;
  padding: 82px 25px 0;
  list-style: none;
  background: #3d3f49;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  position: absolute;
  top: 0;
  z-index: 2;
  left: 0;
  right: 0;
  max-height: 250px;
  -webkit-transition: max-height 0.3s ease, opacity 0.3s ease;
  transition: max-height 0.3s ease, opacity 0.3s ease;
  opacity: 1;
}

.dropDownContainer .locationList.closed {
  padding: 0 25px;
  max-height: 0;
  overflow: hidden;
  opacity: 0;
}

.dropDownContainer .locationList li {
  display: grid;
  grid-template-columns: 1fr 1fr;
  line-height: 1;
  margin-bottom: 20px;
  cursor: pointer;
}

.dropDownContainer .locationList li p {
  margin: 0;
  font-size: 16px;
  -webkit-transition: color 0.3s ease;
  transition: color 0.3s ease;
}

.dropDownContainer .locationList li:hover p {
  color: #00ebc8;
}

.dropDownContainer .locationList li p:last-child {
  text-align: right;
}

.buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 25px;
}

.buttons button {
  width: 100%;
  background: #3d3f49;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  border: none;
}

.buttons button {
  text-decoration: none;
  font-size: 18px;
  padding: 15px 0;
}

.buttons button:hover {
  -webkit-transform: scale(1.025);
          transform: scale(1.025);
}

.buttons a {
  text-decoration: none;
}

.buttons a.disabled {
  opacity: 0.5;
  pointer-events: none;
}

.buttons a button {
  color: #00ebc8;
}

.timesContainer {
  padding: 0 25px 25px;
  position: relative;
  z-index: 3;
}

ul.trainTimes {
  margin: -130px 0 0;
  height: 767px;
  overflow-y: scroll;
  list-style: none;
  padding: 0;
}

ul.trainTimes li {
  margin-bottom: 20px;
  -webkit-filter: drop-shadow(0px 1px 5px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(0px 1px 5px rgba(0, 0, 0, 0.5));
}

ul.trainTimes li .cardTop {
  background: rgba(255, 255, 255, 0.15);
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  padding: 20px;
}

.times,
.cardTop .location {
  display: grid;
}

.times {
  grid-template-columns: 3fr 4fr 3fr;
  margin-bottom: 15px;
}

.times .arrival,
.location .end {
  text-align: right;
}
.times .dots {
  text-align: center;
}

.times .dots ul {
  display: flex;
  list-style: none;
  text-align: center;
  padding: 0;
  margin: 9px auto 0;
  justify-content: space-between;
  opacity: 0.7;
}

.times .dots ul li {
  background: white;
  border-radius: 100%;
  position: relative;
  -webkit-filter: none;
          filter: none;
}

.times .dots ul li:first-child,
.times .dots ul li:last-child {
  width: 4px;
  height: 4px;
  opacity: 0.4;
  top: 2px;
}

.times .dots ul li:nth-child(2),
.times .dots ul li:nth-child(4) {
  width: 6px;
  height: 6px;
  opacity: 0.6;
  top: 1px;
}

.times .dots ul li:nth-child(3) {
  width: 8px;
  height: 8px;
}
.cardTop .location {
  grid-template-columns: 1fr 1fr 1fr;
}

.location .time {
  text-align: center;
}

.times p {
  margin: 0;
  font-size: 24px;
  color: #fff;
  font-weight: 700;
}

.times .delayed p {
  color: #ff2897;
}

.times .delayed s {
  font-size: 16px;
  opacity: 0.5;
}

.times s.fade {
  opacity: 0;
  pointer-events: none;
}

.location p {
  font-size: 12px;
  margin: 0;
  color: #00ebc8;
}

.location p span {
  color: #cccccc;
  opacity: 0.75;
  font-size: 10px;
}

.location .time p {
  font-weight: 700;
  color: #fff;
  font-size: 24px;
  letter-spacing: 1px;
}

ul.callingPoints {
  padding: 10px 20px 15px;
  margin: 20px -20px -20px;
  list-style: none;
  max-height: 450px;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
  opacity: 1;
  background: rgba(255, 255, 255, 0.2);
}

ul.callingPoints.closed {
  max-height: 0;
  overflow: hidden;
  margin: 0 -20px;
  padding: 0;
  opacity: 0;
}

ul.callingPoints li {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  margin: 0;
  padding: 4px 0;
  -webkit-filter: none;
          filter: none;
}

ul.callingPoints li:last-child {
  border-bottom: none;
}

ul.callingPoints li p {
  font-size: 12px;
  margin: 0;
  opacity: 0.75;
}

ul.callingPoints li:last-child p {
  font-weight: 700;
  opacity: 1;
}

ul.callingPoints li p .delayed s {
  opacity: 0.5;
}

ul.callingPoints li p:last-child {
  text-align: right;
}

ul.trainTimes li .cardBottom {
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  background: #3d3f49;
  padding: 8px 20px;
  font-size: 10px;
  display: grid;
  grid-template-columns: 1fr auto;
}

.cardBottom p {
  margin: 0;
  color: rgba(255, 255, 255, 0.65);
  opacity: 0.8;
}

.cardBottom p span.operator {
  color: #fff;
}

.cardBottom p span.location {
  color: #00ebc8;
}

.cardBottom button {
  font-weight: 700;
  color: #ff2897;
  text-decoration: none;
  font-size: 18px;
  background: none;
  border: none;
  height: auto;
  width: auto;
  padding: 0;
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.cardBottom button.spin {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

header {
  height: 258px;
  position: relative;
  padding: 40px 25px;
  box-sizing: border-box;
}

header::before,
header::after {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  content: "";
}

header::before {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#3d3f49), to(#9500ed));
  background-image: linear-gradient(180deg, #3d3f49 0%, #9500ed 100%);
  opacity: 0.5;
  z-index: 1;
}

header::after {
  background-image: url(/OnTrack/static/media/bg.856358a4.jpg);
  opacity: 0.1;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;
  z-index: 2;
}

header .headerLocation,
header .headerCrs {
  display: grid;
  position: relative;
  z-index: 3;
}

header .headerLocation {
  grid-template-columns: 1fr 1fr;
}

header .headerCrs {
  grid-template-columns: 1fr 4fr 1fr;
}

header .headerLocation p {
  color: rgba(255, 255, 255, 0.75);
  font-size: 12px;
  margin: 0;
}

header .headerLocation p:last-child,
header .headerCrs h2:last-child {
  text-align: right;
  -webkit-animation-delay: 0.45s;
          animation-delay: 0.45s;
}

header .headerCrs h2 {
  margin: 0;
  font-weight: 700;
  font-size: 32px;
  -webkit-animation-delay: 0.05s;
          animation-delay: 0.05s;
  letter-spacing: 1px;
}

header .headerCrs h2:last-child {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

header .headerCrs .graphicContainer {
  padding: 2px 15px 0;
  display: grid;
  grid-template-columns: auto 1fr auto;
}

header .headerCrs .graphicContainer ul {
  margin: 0;
  list-style: none;
  padding: 0;
  text-align: center;
  padding-top: 3px;
}

header .headerCrs .graphicContainer ul li {
  display: inline-block;
  margin: 0 7px;
  vertical-align: middle;
  border-radius: 100%;
  background: white;
  position: relative;
}

header .headerCrs .graphicContainer ul:first-child li:last-child,
header .headerCrs .graphicContainer ul:last-child li:first-child {
  width: 8px;
  height: 8px;
}

header .headerCrs .graphicContainer ul li:nth-child(2) {
  width: 6px;
  height: 6px;
  opacity: 0.6;
}

header .headerCrs .graphicContainer ul:first-child li:first-child,
header .headerCrs .graphicContainer ul:last-child li:last-child {
  width: 4px;
  height: 4px;
  opacity: 0.4;
}

header .headerCrs .graphicContainer ul:first-child li:first-child {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

header .headerCrs .graphicContainer ul:first-child li:nth-child(2) {
  -webkit-animation-delay: 0.15s;
          animation-delay: 0.15s;
}

header .headerCrs .graphicContainer ul:first-child li:last-child {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

header .headerCrs img {
  width: 42px;
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
  padding: 0 7px;
  margin: 0 auto;
  position: relative;
  top: 6px;
}

header .headerCrs .graphicContainer ul:last-child li:first-child {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

header .headerCrs .graphicContainer ul:last-child li:nth-child(2) {
  -webkit-animation-delay: 0.35s;
          animation-delay: 0.35s;
}

header .headerCrs .graphicContainer ul:last-child li:last-child {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

@media screen and (max-width: 414px) {
  header .headerCrs h2 {
    font-size: 28px;
  }
}

footer {
  padding: 15px 25px 20px;
  top: calc(100% - 80px);
  position: absolute;
  left: 0;
  right: 0;
  z-index: 99;
  background: transparent;
}

footer button {
  width: 100%;
  background-color: black;
  border-radius: 10px;
  border: none;
  font-size: 18px;
  padding: 10px 0 12px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.25);
}

footer button:hover {
  color: #ff2897;
  -webkit-transform: scale(1.025);
          transform: scale(1.025);
}

